* {
  box-sizing: border-box;
  font-family: "Artifika", serif;
}

html {
  --bg: #1d1d1d;
  --primary: #dac6f8;
  --secondary: #bfadd8;
  --tertiary: #a495b9;
  --quaternary: #897d9a;
  --light-grey: #d3d3d3;

  background-color: var(--bg);
  color: var(--fg);
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica,
    Arial, sans-serif;
  font-size: calc(0.9em + 0.5vw);
  line-height: 1.3;
}

body {
  margin: 0;
  background-color: var(--bg);
  width: 100%;
  height: 100vh;
}

main {
  margin: 0 auto;
  max-width: 25em;
}

/*NavBar*/
.navbar-container {
  display: flex;
  position: sticky;
  top: 0;
  z-index: 20;
  background-color: var(--bg);
  width: 100%;
  padding: 0.5rem;
  align-items: center;
  border-bottom: 1px solid var(--light-grey);
}

.navbar-brand {
  flex: 1;
  height: 50px;
}

.navbar-login-button {
  padding: 0.5rem 1rem;
  font-size: 17px;
  border-radius: 20px;
  cursor: pointer;
  background-color: var(--bg);
  border: 1px solid var(--secondary);
  color: var(--secondary);
}

.navbar-login-button:hover {
  box-shadow: 0 0 4px var(--primary);
}

.blockies {
  border-radius: 50%;
}

.navbar-user {
  padding: 0.5rem 1rem;
  font-size: 17px;
  display: flex;
  align-items: center;
  gap: 0 10px;
  border-radius: 20px;
  cursor: pointer;
  background-color: var(--secondary);
  color: white;
}

/*Post*/
.post-container {
  padding: 0.5rem;
  display: flex;
  gap: 0 10px;
  border-bottom: 1px solid var(--light-grey);
}

.post-details {
  display: flex;
  width: 100%;
  flex-direction: column;
  gap: 10px 0;
}

.post-comments {
  display: flex;
  align-items: center;
  padding: 0 10px;
  gap: 0 5px;
  color: var(--light-grey);
  font-size: 17px;
  border-left: 1px solid;
  border-right: 1px solid;
  border-color: var(--light-grey);
}

.post-creator {
  color: white;
}

.post-comment-content {
  padding: 0.5rem;
  display: flex;
  flex-direction: column-reverse;
  max-height: 300px;
  overflow-y: scroll;
  gap: 0 10px;
}

.post-creator:hover {
  color: var(--primary);
  cursor: pointer;
  text-decoration: underline;
}

.post-comments-list {
  display: flex;
  flex-direction: column-reverse;
}

.post-image {
  width: 100%;
  object-fit: cover;
  border-radius: 10px;
}

.divider {
  width: 100%;
  height: 1px;
  background-color: var(--light-grey);
}

.post-content {
  font-size: 14px;
  color: white;
  padding-bottom: 20px;
}

.action-section {
  display: flex;
  gap: 10px;
}

.comment-input {
  background-color: transparent;
  border: none;
  outline: none;
  font-size: 14px;
  flex: 1;
  color: var(--light-grey);
}

.comment-container {
  display: flex;
  border: 1px solid var(--light-grey);
  padding: 10px;

  flex: 1;
  border-radius: 20px;
  color: var(--light-grey);
}

.see-more-button {
  display: flex;
  gap: 0 10px;
  border: 1px solid var(--light-grey);
  padding: 10px;
  border-radius: 10px;
  font-size: 12px;
  color: var(--light-grey);
  cursor: pointer;
}

/*HomePage*/
.main-section {
  display: flex;
  height: 100%;
  width: 100%;
}

.view-section {
  flex: 0.7;
  height: 85vh;
  overflow-y: scroll;
  border-right: 1px solid var(--light-grey);
}

.create-post-button {
  padding: 0.5rem 0.7rem;
  font-size: 17px;
  width: max-content;
  margin: 1rem;
  border-radius: 20px;
  cursor: pointer;
  background-color: var(--bg);
  border: 1px solid var(--secondary);
  color: var(--secondary);
}

.create-post-button:hover {
  box-shadow: 0 0 4px var(--primary);
}

.sidebar-section {
  height: 85vh;
  flex: 0.3;
}

.following-section {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 1rem;
}

.following-title {
  color: white;
}

.search-input {
  background-color: transparent;
  border: 1px solid var(--light-grey);
  outline: none;
  flex: 1;
  padding: 10px;
  font-size: 14px;
  color: var(--light-grey);
  padding-right: 30px;
  border-radius: 20px;
  background-image: url("./assets/search.svg");
  background-position: left 10px center;
  padding-left: 40px;
  background-size: 15px;
  background-repeat: no-repeat;
}

/*User*/
.user-tile {
  display: flex;
  gap: 0 10px;
  align-items: center;
  padding-bottom: 10px;
}

.user-tile:hover {
  text-decoration: underline;
  color: white;
  cursor: pointer;
}

.user-account {
  color: white;
  font-size: 20px;
}

.user-name {
  color: white;
  font-size: 15px;
}

/*Profile*/
.profile-container {
  padding: 1rem;
}

.profile-user-tile {
  display: flex;
  gap: 0 10px;
  align-items: center;
  padding-bottom: 20px;
}

.profile-user-name {
  color: white;
  font-size: 35px;
}
.profile-user-account {
  color: white;
  font-size: 30px;
}

.profile-user-desc {
  color: white;
  font-size: 18px;
  line-height: 14px;
}

.profile-logout {
  padding: 0.5rem 1rem;
  font-size: 17px;
  border-radius: 20px;
  height: fit-content;
  cursor: pointer;
  background-color: var(--bg);
  border: 1px solid var(--secondary);
  color: var(--secondary);
}

.follow-text:hover {
  color: var(--primary);
  cursor: pointer;
  text-decoration: underline;
}

/*Create Post*/
.dialog-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
}

.dialog {
  width: 800px;
  background-color: var(--bg);
  border-radius: 20px;
  border: 1px solid var(--light-grey);
}

.dialog-title-section {
  display: flex;
  gap: 10px;
  align-items: center;
  padding: 20px;
  color: var(--light-grey);
}
.dialog-title {
  flex: 1;
}

.create-input {
  margin: 0 20px;
  padding: 10px;
  font-size: 14px;
  color: var(--light-grey);
  border: 1px solid var(--light-grey);
  border-radius: 10px;
  width: calc(100% - 40px);
  height: 200px;
  outline: none;
  background: transparent;
}

.create-file-input {
  margin: 0 20px;
  padding: 10px;
  font-size: 14px;
  color: var(--light-grey);
  border: 1px solid var(--light-grey);
  border-radius: 10px;
  width: calc(100% - 40px);
  outline: none;
  background: transparent;
}

.create-button {
  margin: 20px;
  padding: 10px;
  font-size: 14px;
  color: black;
  background: var(--primary);
  width: min-content;
  cursor: pointer;
  border-radius: 10px;
}
.loader,
.loader:before,
.loader:after {
  background: #ffffff;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}

.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
}
.loader {
  color: #ffffff;
  position: fixed;
  text-indent: -9999em;
  margin: 88px auto;
  font-size: 11px;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  position: absolute;
  top: 0;
  content: "";
}
.loader:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loader:after {
  left: 1.5em;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}

/* Comment Tile */
.comment-tile {
  display: flex;
  gap: 0 10px;
  padding-bottom: 10px;
}

.comment-creator {
  color: white;
  font-size: 10px;
}

.comment-creator:hover {
  text-decoration: underline;
  cursor: pointer;
}

.comment-content {
  color: white;
  font-size: 15px;
}

/* Login */

.context {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 100;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.content-dialog {
  display: flex;
  flex-direction: column;
  width: 400px;
  height: 400px;
  gap: 20px 0;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.2);
}

.dialog-action {
  display: flex;
  flex-direction: column;
  gap: 10px 0;
  width: 80%;
  align-items: center;
  padding: 20px;
  color: var(--light-grey);
}

.dialog-button {
  padding: 10px;
  font-size: 14px;
  width: 100%;
  text-align: center;
  color: black;
  color: var(--primary);
  cursor: pointer;
  border-radius: 10px;
  border: 1px solid var(--primary);
}

.dialog-button.filled {
  background: var(--primary);
  color: var(--bg);
}

.dialog-button:hover {
  box-shadow: 0 0 10px var(--primary);
}

.context h1 {
  text-align: center;
  color: #fff;
  font-size: 50px;
}

.area {
  background: #4e54c8;
  background: -webkit-linear-gradient(
    to left,
    var(--primary),
    var(--secondary)
  );
  width: 100%;
  height: 100vh;
}

.circles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.circles li {
  position: absolute;
  display: block;
  list-style: none;
  width: 20px;
  height: 20px;
  background: rgba(255, 255, 255, 0.2);
  animation: animate 25s linear infinite;
  bottom: -150px;
}

.circles li:nth-child(1) {
  left: 25%;
  width: 80px;
  height: 80px;
  animation-delay: 0s;
}

.circles li:nth-child(2) {
  left: 10%;
  width: 20px;
  height: 20px;
  animation-delay: 2s;
  animation-duration: 12s;
}

.circles li:nth-child(3) {
  left: 70%;
  width: 20px;
  height: 20px;
  animation-delay: 4s;
}

.circles li:nth-child(4) {
  left: 40%;
  width: 60px;
  height: 60px;
  animation-delay: 0s;
  animation-duration: 18s;
}

.circles li:nth-child(5) {
  left: 65%;
  width: 20px;
  height: 20px;
  animation-delay: 0s;
}

.circles li:nth-child(6) {
  left: 75%;
  width: 110px;
  height: 110px;
  animation-delay: 3s;
}

.circles li:nth-child(7) {
  left: 35%;
  width: 150px;
  height: 150px;
  animation-delay: 7s;
}

.circles li:nth-child(8) {
  left: 50%;
  width: 25px;
  height: 25px;
  animation-delay: 15s;
  animation-duration: 45s;
}

.circles li:nth-child(9) {
  left: 20%;
  width: 15px;
  height: 15px;
  animation-delay: 2s;
  animation-duration: 35s;
}

.circles li:nth-child(10) {
  left: 85%;
  width: 150px;
  height: 150px;
  animation-delay: 0s;
  animation-duration: 11s;
}

@keyframes animate {
  0% {
    transform: translateY(0) rotate(0deg);
    opacity: 1;
    border-radius: 0;
  }

  100% {
    transform: translateY(-1000px) rotate(720deg);
    opacity: 0;
    border-radius: 50%;
  }
}

.login-input {
  background-color: rgba(255, 255, 255, 0.2);
  border: none;
  outline: none;
  border-radius: 10px;
  padding: 10px;
  color: white;
  font-size: 14px;
  width: 100%;
}

.login-textarea {
  background-color: rgba(255, 255, 255, 0.2);
  border: none;
  outline: none;
  border-radius: 10px;
  padding: 10px;
  color: white;
  font-size: 14px;
  width: 100%;
  height: 100px;
}

.login-input::placeholder {
  color: var(--light-grey);
}

.login-textarea::placeholder {
  color: var(--light-grey);
}

/* User Details */
.post-section {
  flex: 0.7;
  border-right: 1px solid var(--light-grey);
}

.followers-section {
  flex: 0.3;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 10px 0;
}
